<template>
  <section>
    <div class="container py-5">
      <div class="d-flex">
        <button
          type="button"
          class="btn btn-primary text-white btn-sm"
          data-bs-toggle="modal"
          data-bs-target="#productModal"
          @click="statusModal('post', '')"
        >
          
        </button>
      </div>
      <div class="table-responsive">
        <table class="table table-responsive">
          <thead>
            <tr>
              <th scope="col" class="ps-3" width="10%"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col" width="10%"></th>
              <th scope="col" class="text-center" width="10%"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in coupons" :key="item.id" class="border-white">
              <th scope="row">{{ item.title }}</th>
              <td>{{ item.percent }}</td>
              <td>{{ item.code }}</td>
              <td>
                {{ new Date(item.due_date).toLocaleString().split(' ')[0] }}
              </td>
              <td :class="{ 'text-success': item.is_enabled }">
                {{ item.is_enabled ? '' : '' }}
              </td>
              <td align="right">
                <div class="btn-group">
                  <button
                    type="button"
                    class="btn btn-outline-gray btn-sm"
                    data-bs-toggle="modal"
                    data-bs-target="#productModal"
                    @click="statusModal('put', item)"
                  >
                    
                  </button>
                  <button
                    type="button"
                    class="btn btn-outline-danger btn-sm"
                    data-bs-toggle="modal"
                    data-bs-target="#deleteModal"
                    @click="statusModal('delete', item)"
                  >
                    
                  </button>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <CouponModal :status="status" :coupon="tempCoupon" @get-data="getCoupons" />
  </section>
</template>

<script>
import CouponModal from '@/components/backend/CouponModal.vue';

export default {
  data() {
    return {
      status: '',
      coupons: [],
      tempCoupon: {},
    };
  },
  components: {
    CouponModal,
  },
  methods: {
    getCoupons(num = 1) {
      this.$store.dispatch('updateLoading', true);
      const url = `${process.env.VUE_APP_APIURL}/api/${process.env.VUE_APP_APIPATH}/admin/coupons?page=${num}`;
      this.axios.get(url)
        .then((res) => {
          if (res.data.success) {
            this.coupons = res.data.coupons;
          } else {
            this.$swal({ title: res.data.message, icon: 'error' });
            this.$router.push('/login');
          }
          this.$store.dispatch('updateLoading', false);
        })
        .catch(() => {
          this.$swal({ title: '', icon: 'error' });
          this.$store.dispatch('updateLoading', false);
        });
    },
    statusModal(status, data) {
      this.status = status;
      this.tempCoupon = status === 'post' ? {} : JSON.parse(JSON.stringify(data));
    },
  },
  mounted() {
    this.getCoupons();
  },
};
</script>
